body {
  background-color: white;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}
a{
  text-decoration: none;
}
.navbar{
  width: 100%;
  height: 47px;
  line-height: 47px;
  background-color: blueviolet;
  text-align: center;
  word-spacing: 15px;
  position: relative;
  float: left;
}
.navitem{
  color: white;
  font-weight: 400;
}
.main{
  width: 100%;
  background-color: rgb(236, 223, 247);
  position: relative;
  float: left;

}
.bg-content{
  text-align: center;
  margin-top: 10%;
  position: relative;
  margin-bottom: 20%;
}
progress{
  width: 100%;
  height: 45px;
}
.services{
  display: flex;
  justify-content: space-evenly;
}
.services-card{
  width: 100%;
  text-align: center;
  background-color: white;
  border-radius: 10px;
  padding: 12px;
  cursor: pointer;
  box-shadow: 0px 2px 6px rgba(0,0,0,0.3);
}
.services-card:hover{
  background-color: blueviolet;
  color: white;
}
.review{
  display: flex;
  justify-content: space-around;

}
.review-item{
  width: 15%;
  text-align: center;
  background-color: white;
  border-radius: 10px;
  padding: 12px;
  cursor: pointer;
  box-shadow: 0px 2px 6px rgba(0,0,0,0.3);
}
.hire-bg{
  width: 100%;
  height: 600px;
  position: relative;
  float: left;
  background-image: url(/public/images/hirebg.gif);
  background-attachment: fixed;
}
.dark-scarf{
  width: 100%;
  height: inherit;
  position: relative;
  float: left;
  background-color: blueviolet;
  opacity: 0.5;
}
@media only screen and (max-width:963px){
  .review{
    display: block;
  }
  .review-item{
    width: 100%;
    margin-bottom: 5%;
    padding: 6px;
  }
}
.footer{
  width: 100%;
  height: 600px;
  background-color: black;
  position: relative;
  float: left;
}
.container{
  width: 92%;
  margin: 4%;
  position: relative;
  float: left;
}